.block {
  height: 100%;
  padding: 110px 70px 100px 70px;

  display: flex;
  flex-direction: column;
  align-content: center;
  background-color: #f5f5f5;
}

.block__title {
  margin: 0;
  padding: 0 0 23px 0;

  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
  /* identical to box height */

  letter-spacing: -0.04em;
  text-align: left;

  color: #000000;

  border-bottom: #000000 1px solid;
}
.line {
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  padding: 90px 0 90px 0;
  font-style: normal;
  font-weight: 400;
  font-size: 50px;
  line-height: 58px;
  /* identical to box height, or 116% */

  text-align: center;
  letter-spacing: -0.04em;

  color: #000000;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.item {
  width: 90px;
  height: 60px;
  margin: 0 5px;

  border-radius: 10px;
  background-color: #e8e8e8;

  display: flex;
  justify-content: center;
  flex-direction: column;

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  text-align: center;
  letter-spacing: -0.04em;

  color: #000000;
}

.box_line {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  .line_title {
    width: 120px;
    height: 60px;
    margin: 0 5px;

    border-radius: 10px;
    background-color: #e8e8e8;

    display: flex;
    justify-content: center;
    flex-direction: column;

    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    /* identical to box height */

    text-align: center;
    letter-spacing: -0.04em;

    color: #800080;

    .line_title {
      font-style: normal;
      font-weight: 300;
      font-size: 20px;
      line-height: 58px;
      /* identical to box height, or 116% */

      text-align: center;
      letter-spacing: -0.04em;

      color: #464545;
    }
  }
}

@media screen and (max-width: 768px) {
  .title {
    padding: 78px 0 78px 0;
  }

  .item {
    width: 84px;
    height: 57px;

    font-size: 12px;
    line-height: 15px;
  }
}

@media screen and (max-width: 580px) {
  .tech {
    background-color: #ffffff;
  }

  .wrapper {
    padding-right: 4px;
  }

  .container {
    max-width: 178px;
    gap: 10px;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .item {
    margin: 0;
  }

  .title {
    padding: 59px 0;
    font-size: 30px;
    line-height: 36px;
  }
}
